<template>
  <div class="select-unit-modal" v-if="showModal">
    <div class="modal-header"></div>

    <div class="modal-body">
      <div class="header">
        <input
          v-model="search.text"
          type="text"
          placeholder="Search creature"
          @input="searchUnit()"
          @keyup.enter="selectFirstFounded()"
        />

        <button class="close-modal-button" @click="closeModal">×</button>
      </div>

      <div class="units">
        <section
          v-for="(town, index) in towns"
          :key="town"
          :class="`${town}-town`"
        >
          <button v-for="i in 14" :key="i">
            <Portrait
              path="/images/creatures_portraits"
              :file="creatures[index * 14 + i - 1].name_en"
              width="58px"
              height="64px"
              @click="selectUnit"
            />
          </button>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  components: {
    Portrait: () => import("@/components/damageCalculator/Portrait.vue")
  },
  props: {
    showModal: {
      value: Boolean,
      default: false,
      required: false
    },
    creatures: {
      value: Array,
      default: [],
      required: false
    }
  },
  data() {
    return {
      search: {
        text: ""
      },
      selected: {
        id: 0,
        name: ""
      },
      towns: [
        "castle",
        "rampart",
        "tower",
        "inferno",
        "necropolis",
        "dungeon",
        "stronghold",
        "fortress",
        "conflux",
        "cove"
      ]
    };
  },
  methods: {
    searchUnit() {},
    selectFirstFounded() {},
    selectUnit(unit) {
      this.selected = this.creatures.filter(
        creature => creature.name_en === unit
      )[0];
      this.$emit("select", this.selected);
      this.$emit("close");
    },
    closeModal() {
      this.$emit("close");
    }
  }
};
</script>

<style lang="scss">
.select-unit-modal {
  position: fixed;
  margin-top: 20px;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(255, 255, 255);

  .modal-body {
    user-select: none;
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;

    .header {
      display: flex;
      margin-bottom: 10px;

      input {
        @extend %input;
        width: 200px;
        padding: 0.25rem 0.5rem;
        font-size: 1rem;
        line-height: 1.5;
      }

      .close-modal-button {
        @extend %button;
        padding: 1rem 1rem;
        font-size: 1.5rem;
        font-weight: 700;
        line-height: 1;
        color: #000;
        text-shadow: 0 1px 0 #fff;
        opacity: 0.5;
        margin-left: auto;
      }
    }
  }

  .units {
    display: flex;
    flex-direction: column;

    div,
    section {
      display: flex;
      margin-bottom: 5px;
    }

    button {
      @extend %button;
      padding: 0.25rem 0.5rem;
    }

    img {
      cursor: pointer;
      border-radius: 5px;

      &:hover {
        border: 1px solid black;
        box-shadow: 0 0 5px black;
      }
    }
  }
}
</style>
