<template>
  <input type="number" min="0" max="9999" v-model="count" />
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  props: {
    side: {
      value: String,
      default: "",
      required: true,
      validator(value) {
        return value.toLowerCase();
      }
    }
  },
  computed: {
    ...mapGetters({
      attacker: "calculator/attacker",
      defender: "calculator/defender"
    }),
    count: {
      get() {
        return parseInt(this[this.side].creature.count);
      },
      set(count) {
        if (count < 0 || count > 99) {
          count = 0;
        }
        count = parseInt(count);
        return this.setCreaturesCount({
          side: this.side,
          count: count
        });
      }
    }
  },
  methods: {
    ...mapActions({
      setCreaturesCount: "calculator/setCreaturesCount"
    })
  }
};
</script>

<style lang="scss">
.input-stat {
  .stat-name {
    display: block;
    margin-bottom: 5px;
  }

  input {
    width: 60px;
    border: 1px solid rgb(206, 212, 218);
    padding: 0.25rem 0.5rem;
    font-size: 0.875rem;
    line-height: 1.5;
    border-radius: 0.2rem;
    font-size: inherit;
    font-family: inherit;
  }
}
</style>
