<template>
  <div class="input-stat">
    <span class="stat-name">{{ stat | capitalizeFirstChar }}</span>
    <input type="number" min="0" max="99" v-model="statInput" />
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
  props: {
    side: {
      value: String,
      default: "",
      required: true
    },
    stat: {
      value: String,
      default: "",
      required: true
    }
  },
  computed: {
    ...mapGetters({
      attacker: "calculator/attacker",
      defender: "calculator/defender"
    }),
    statInput: {
      get() {
        return parseInt(this[this.side].hero.stats[this.stat]);
      },
      set(value) {
        if (value < 0 || value > 99) {
          value = 0;
        }
        value = parseInt(value);
        return this.setHeroStat({
          side: this.side,
          stat: this.stat,
          value: value
        });
      }
    }
  },
  methods: {
    ...mapActions({
      setHeroStat: "calculator/setHeroStat"
    })
  },
  filters: {
    capitalizeFirstChar: value => {
      if (!value) {
        return "";
      }
      value = value.toString();
      return value.charAt(0).toUpperCase() + value.slice(1);
    }
  }
};
</script>

<style lang="scss" scoped>
.input-stat {
  .stat-name {
    display: block;
    margin-bottom: 10px;
  }

  input {
    width: 60px;
    border: 1px solid rgb(206, 212, 218);
    padding: 0.25rem 0.5rem;
    font-size: 0.875rem;
    line-height: 1.5;
    border-radius: 0.2rem;
    font-size: inherit;
    font-family: inherit;
  }
}
</style>
