<template>
  <section class="damage-calculator-page">
    <section class="tabs">
      <div
        class="tab"
        v-for="(calculator, index) in calculators"
        :key="index"
        :class="{ active: index === activeCalculator }"
        @click="setActiveCalculator(index)"
      >
        {{ calculatorTitle }}
      </div>

      <button
        class="add-tab"
        :class="{ disabled: calculators.length >= 5 }"
        @click="addCalculator"
      >
        +
      </button>
    </section>

    <DamageCalculator
      v-for="(calculator, index) in calculators"
      :key="index"
      v-show="index === activeCalculator"
    />

    <PageFooter />
  </section>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import DamageCalculator from "@/components/DamageCalculator.vue";

export default {
  components: {
    DamageCalculator,
    PageFooter: () => import("@/components/PageFooter.vue")
  },
  mounted() {
    this.getHeroes();
    this.getCreatures();
    this.getTerrains();
  },
  computed: {
    ...mapGetters({
      calculators: "calculator/calculators",
      activeCalculator: "calculator/activeCalculator",
      attacker: "calculator/attacker",
      defender: "calculator/defender",
      options: "calculator/options"
    }),
    calculatorTitle() {
      let title = "";
      if (this.attacker.hero.name) {
        title.concat(this.attacker.hero.name);
      }
      if (this.attacker.creature.name) {
        title.concat(this.attacker.creature.name);
      }
      if (title.length === 0) {
        title = "Damage Calculator";
      }
      return title;
    }
  },
  methods: {
    ...mapActions({
      getHeroes: "data/getHeroes",
      getCreatures: "data/getCreatures",
      getTerrains: "data/getTerrains",
      addCreature: "calculator/addCreature",
      setHeroAttack: "calculator/setHeroAttack",
      addCalculator: "calculator/addCalculator",
      setActiveCalculator: "calculator/setActiveCalculator"
    })
  }
};
</script>

<style lang="scss" scoped>
section[class="damage-calculator-page"] {
  margin: 20px auto 0 auto;
  min-width: 320px;
  max-width: 2000px;
  width: 90%;
}

section[class="tabs"] {
  display: flex;
  flex-direction: row;
  user-select: none;

  .tab {
    flex: 1 0 0;
    display: flex;
    align-items: center;
    max-width: 170px;
    height: 30px;
    background-color: white;
    margin-right: 5px;
    border-right: 1px solid black;
    text-align: center;
    padding: 5px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;

    &.active {
      box-shadow: 0 0 5px rgb(170, 170, 170);
      border-top-right-radius: 5px;
      border-top-left-radius: 5px;
      border: none;
    }

    &:hover {
      box-shadow: 0 0 5px rgb(170, 170, 170);
    }
  }

  .add-tab {
    @extend %button;
    font-weight: bold;
    font-size: 22px;

    &.disabled {
      cursor: not-allowed;
      pointer-events: none;
      background-color: rgba(0, 170, 0, 0.2);
    }
  }
}
</style>
