<template>
  <section class="damage-calculator">
    <section class="damage-calculator-body">
      <section class="attacker">
        <section class="header">
          <h2>{{ $t("attacker") }}</h2>
          <button
            class="pick-creature-button attacker"
            @click="showAttackerModal = true"
          >
            {{ $t("pick-creature") }}
          </button>
        </section>

        <section class="unit-and-hero">
          <div class="unit">
            <button @click="showAttackerModal = true">
              <Portrait
                v-if="Object.entries(attacker.creature).length === 0"
                path="/images/creatures_portraits"
                file="Archangel"
              />
              <Portrait
                v-else
                path="/images/creatures_portraits"
                :file="attacker.creature.name_en"
              />
            </button>

            <InputCreaturesCount
              side="attacker"
              v-show="Object.entries(attacker.creature).length > 0"
            />
          </div>

          <div class="hero">
            <SelectHero
              v-if="Object.entries(attacker.creature).length > 0"
              :heroes="data.heroes"
            />

            <div
              class="hero-params"
              v-show="Object.entries(attacker.creature).length > 0"
            >
              <InputHeroStat class="level" stat="level" side="attacker" />
              <InputHeroStat class="attack" stat="attack" side="attacker" />
              <InputHeroStat class="defense" stat="defense" side="attacker" />
            </div>
          </div>
        </section>

        <section
          class="hero-skills"
          v-show="Object.entries(attacker.creature).length > 0"
        >
          <SelectSkill
            v-for="skill in heroSkills"
            :key="skill"
            :skill="skill"
            @click="setHeroSkill({ side: 'attacker', skill: skill, value: $event })"
          />
        </section>

        <section
          class="unit-effects"
          v-show="Object.entries(attacker.creature).length > 0"
        >
          <div class="attack-buffs">
            <InputUnitEffect
              v-for="effect in unitsAttackPositiveEffects"
              :key="effect"
              :name="effect"
              @click="
                $event
                  ? addUnitEffect({ side: 'attacker', effect })
                  : removeUnitEffect({ side: 'attacker', effect })
              "
            />
          </div>

          <div class="defend-buffs">
            <InputUnitEffect
              v-for="effect in unitsDefensePositiveEffects"
              :key="effect"
              :name="effect"
              @click="
                $event
                  ? addUnitEffect({ side: 'attacker', effect })
                  : removeUnitEffect({ side: 'attacker', effect })
              "
            />
          </div>

          <div class="attack-debuffs">
            <InputUnitEffect
              v-for="effect in unitsAttackNegativeEffects"
              :key="effect"
              :name="effect"
              @click="
                $event
                  ? addUnitEffect({ side: 'attacker', effect })
                  : removeUnitEffect({ side: 'attacker', effect })
              "
            />
          </div>
        </section>

        <section
          class="total-damage"
          v-show="Object.entries(attacker.creature).length > 0"
        >
          <span>Damage: {{ attacker.totalDamage }}</span>
          <span>Kills: {{ attacker.totalKills }}</span>
        </section>

      </section>

      <section class="defender">
        <section class="header">
          <button
            class="pick-creature-button defender"
            @click="showDefenderModal = true"
          >
            {{ $t("pick-creature") }}
          </button>
          <h2>{{ $t("defender") }}</h2>
        </section>

        <section class="unit-and-hero">
          <div class="hero">
            <SelectHero
              v-if="Object.entries(defender.creature).length > 0"
              :heroes="data.heroes"
            />

            <div
              class="hero-params"
              v-show="Object.entries(defender.creature).length > 0"
            >
              <InputHeroStat class="level" stat="level" side="defender" />
              <InputHeroStat class="attack" stat="attack" side="defender" />
              <InputHeroStat class="defense" stat="defense" side="defender" />
            </div>
          </div>

          <div class="unit">
            <button @click="showAttackerModal = true">
              <Portrait
                v-if="Object.entries(defender.creature).length === 0"
                path="/images/creatures_portraits"
                file="Archangel"
              />
              <Portrait
                v-else
                path="/images/creatures_portraits"
                :file="defender.creature.name_en"
              />
            </button>

            <InputCreaturesCount
              side="defender"
              v-if="Object.entries(defender.creature).length > 0"
            />
          </div>
        </section>

        <section
          class="hero-skills"
          v-show="Object.entries(defender.creature).length > 0"
        >
          <SelectSkill
            v-for="skill in heroSkills"
            :key="skill"
            :skill="skill"
            @click="setHeroSkill({ side: 'defender', skill: skill, value: $event })"
          />
        </section>

        <section
          class="total-damage"
          v-show="Object.entries(defender.creature).length > 0"
        >
          <span>Damage: {{ defender.totalDamage }}</span>
          <span>Kills: {{ defender.totalKills }}</span>
        </section>

      </section>
    </section>

    <section class="damage-calculator-footer">
      <SelectTerrain class="select-terrain" :terrains="data.terrains" />
    </section>

    <SelectUnitModal
      v-if="showAttackerModal"
      :showModal="showAttackerModal"
      :creatures="data.creatures"
      @close="showAttackerModal = false"
      @select="setActiveCreature({ side: 'attacker', creature: $event })"
    />
    <SelectUnitModal
      v-if="showDefenderModal"
      :showModal="showDefenderModal"
      :creatures="data.creatures"
      @close="showDefenderModal = false"
      @select="setActiveCreature({ side: 'defender', creature: $event })"
    />
  </section>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import SelectUnitModal from "@/components/damageCalculator/SelectUnitModal.vue";
import SelectSkill from "@/components/damageCalculator/SelectSkill.vue";
import InputUnitEffect from "@/components/damageCalculator/InputUnitEffect.vue";
import InputHeroStat from "@/components/damageCalculator/InputHeroStat.vue";
import InputCreaturesCount from "@/components/damageCalculator/InputCreaturesCount.vue";

export default {
  components: {
    // selectHeroSkills,
    SelectUnitModal,
    SelectSkill,
    InputUnitEffect,
    InputHeroStat,
    SelectHero: () => import("@/components/damageCalculator/SelectHero.vue"),
    Portrait: () => import("@/components/damageCalculator/Portrait.vue"),
    SelectTerrain: () =>
      import("@/components/damageCalculator/SelectTerrain.vue"),
    InputCreaturesCount
  },
  data() {
    return {
      showAttackerModal: false,
      showDefenderModal: false,
      unitsAttackPositiveEffects: [
        "Bless",
        "Bloodlust",
        "Frenzy",
        "Prayer",
        "Precision",
        "Slayer"
      ],
      unitsDefensePositiveEffects: ["Shield", "Stone Skin", "Air Shield"],
      unitsAttackNegativeEffects: ["Curse", "Weakness", "Disrupting Ray"],

      heroSkills: ["Offense", "Air", "Armorer", "Fire", "Archery", "Earth", "Artillery", "Water"]
    };
  },
  computed: {
    ...mapGetters({
      activeCalculator: "calculator/activeCalculator",
      attacker: "calculator/attacker",
      defender: "calculator/defender",
      options: "calculator/options",
      data: "data/data"
    })
  },
  methods: {
    ...mapActions({
      addCreature: "calculator/addCreature",
      setHeroAttack: "calculator/setHeroAttack",
      setCreaturesCount: "calculator/setCreaturesCount",
      setActiveCreature: "calculator/setActiveCreature"
    }),
    calculate() {
      let minDamage,
        averageDamage,
        maxDamage,
        minKills,
        averageKills,
        maxKills = 0;

      this.attacker.creature.map(creature => {
        creature = this.calculateCurseModificator(creature);
        creature = this.calculateStatsModification(creature);
        creature = this.calculateStatsModification(creature);
        creature = this.calculateTotalDamagesModification(creature);
        creature = this.calculateWithHates(creature);
        creature = this.calculateArtillery(creature);
        creature = this.unitsFeatures(creature);

        // If ATTACKER attack > DEFENDER defense
        // Then base formula: ATTACKER units count * ATTACKER unit damage * (1 + 0.05 * (ATTACKER attack - DEFENDER defense))
        if (creature.attack > this.defender.defense) {
          // Max attack cap = 3
          let attackDefenseDifference = Math.abs(
            0.05 * (creature.attack - this.defender.defense)
          );

          if (attackDefenseDifference > 3) {
            attackDefenseDifference = 3;
          }

          minDamage = Math.abs(
            creature.baseMinDamage *
              ((1 + attackDefenseDifference + this.attacker.damageBonus) *
                (1 - this.defender.defenseBonus) *
                (1 - this.defender.defenseMagicBonus))
          );
          this.attacker.totalMaxDamage = Math.abs(
            this.attacker.baseMaxDamage *
              ((1 + attackDefenseDifference + this.attacker.damageBonus) *
                (1 - this.defender.defenseBonus) *
                (1 - this.defender.defenseMagicBonus))
          );
        } else if (creature.attack === this.defender.defense) {
          this.attacker.totalMinDamage = Math.abs(
            this.attacker.baseMinDamage *
              ((1 + this.attacker.damageBonus) *
                (1 - this.defender.defenseBonus) *
                (1 - this.defender.defenseMagicBonus))
          );
          this.attacker.totalMaxDamage = Math.abs(
            this.attacker.baseMaxDamage *
              ((1 + this.attacker.damageBonus) *
                (1 - this.defender.defenseBonus) *
                (1 - this.defender.defenseMagicBonus))
          );
        } else if (creature.attack < this.defender.defense) {
          // base formula: ATTACKER units count * ATTACKER unit damage * (1 - 0.025 * (DEFENDER defense - ATTACKER attack))
          // Max attack cap = 0.7
          let attackDefenseDifference = Math.abs(
            (this.defender.defense - creature.attack) * 0.025
          );

          if (attackDefenseDifference > 0.7) {
            attackDefenseDifference = 0.7;
          }

          this.attacker.totalMinDamage = Math.abs(
            this.attacker.baseMinDamage *
              ((1 + this.attacker.damageBonus) *
                (1 - attackDefenseDifference) *
                (1 - this.defender.defenseBonus) *
                (1 - this.defender.defenseMagicBonus))
          );
          this.attacker.totalMaxDamage = Math.abs(
            this.attacker.baseMaxDamage *
              ((1 + this.attacker.damageBonus) *
                (1 - attackDefenseDifference) *
                (1 - this.defender.defenseBonus) *
                (1 - this.defender.defenseMagicBonus))
          );
        }

        this.attacker.totalMinDamage *= creature.count;
        this.attacker.totalMaxDamage *= creature.count;
      });

      // For DEFENDER

      // If DEFENDER attack > ATTACKER defense
      // Then base formula:
      // DEFENDER units count * DEFENDER unit damage * (1 + 0.05 * (DEFENDER attack - ATTACKER defense))

      // Else if DEFENDER attack < ATTACKER defense
      // Then base formula:
      // DEFENDER units count * DEFENDER unit damage * (1 - 0.025 * (ATTACKER defense - DEFENDER attack))
      if (this.defender.attack > this.attacker.defense) {
        // Max attack cap = 3
        let attackDefenseDifference =
          0.05 * (this.defender.attack - this.attacker.defense);

        if (attackDefenseDifference > 3) {
          attackDefenseDifference = 3;
        }

        this.defender.totalMinDamage = Math.abs(
          this.defender.baseMinDamage *
            ((1 + attackDefenseDifference + this.defender.damageBonus) *
              (1 - this.attacker.defenseBonus) *
              (1 - this.attacker.defenseMagicBonus))
        );
        this.defender.totalMaxDamage = Math.abs(
          this.defender.baseMaxDamage *
            ((1 + attackDefenseDifference + this.defender.damageBonus) *
              (1 - this.attacker.defenseBonus) *
              (1 - this.attacker.defenseMagicBonus))
        );
      } else if (this.defender.attack === this.attacker.defense) {
        // if === then no cap
        this.defender.totalMinDamage = Math.abs(
          this.attacker.baseMinDamage *
            ((1 + this.defender.damageBonus) *
              (1 - this.attacker.defenseBonus) *
              (1 - this.attacker.defenseMagicBonus))
        );
        this.defender.totalMaxDamage = Math.abs(
          this.attacker.baseMaxDamage *
            ((1 + this.defender.damageBonus) *
              (1 - this.attacker.defenseBonus) *
              (1 - this.attacker.defenseMagicBonus))
        );
      } else if (this.defender.attack < this.attacker.defense) {
        // Max attack cap = 0.7
        let attackDefenseDifference =
          (this.attacker.defense - this.defender.attack) * 0.025;

        if (attackDefenseDifference > 0.7) {
          attackDefenseDifference = 0.7;
        }

        this.defender.totalMinDamage = Math.abs(
          this.defender.baseMinDamage *
            ((1 + this.defender.damageBonus) *
              (1 - attackDefenseDifference) *
              (1 - this.attacker.defenseBonus) *
              (1 - this.attacker.defenseMagicBonus))
        );
        this.defender.totalMaxDamage = Math.abs(
          this.defender.baseMaxDamage *
            ((1 + this.defender.damageBonus) *
              (1 - attackDefenseDifference) *
              (1 - this.attacker.defenseBonus) *
              (1 - this.attacker.defenseMagicBonus))
        );
      }

      this.defender.totalMinDamage *= this.$store.state.calculator.defender.unit.count;
      this.defender.totalMaxDamage *= this.$store.state.calculator.defender.unit.count;

      // Transform total damages
      minDamage = Math.floor(minDamage);
      maxDamage = Math.floor(maxDamage);
      averageDamage = Math.floor(minDamage + maxDamage) / 2;

      minKills = Math.floor(
        minDamage / this.$store.state.calculator.defender.unit.stats.health
      );
      maxKills = Math.floor(
        maxDamage / this.$store.state.calculator.defender.unit.stats.health
      );
      averageKills = Math.floor(
        averageDamage / this.$store.state.calculator.defender.unit.stats.health
      );

      // minDamage = Math.floor(minDamage);
      // maxDamage = Math.floor(maxDamage);
      // averageDamage = Math.floor(minDamage + maxDamage) / 2;

      // minKills = Math.floor(minDamage / this.$store.state.calculator.defender.unit.stats.health);
      // maxKills = Math.floor(maxDamage / this.$store.state.calculator.defender.unit.stats.health);
      // averageKills = Math.floor(averageDamage / this.$store.state.calculator.defender.unit.stats.health);

      this.setAttackerDamage({
        minDamage,
        averageDamage,
        maxDamage,
        minKills,
        averageKills,
        maxKills
      });

      this.setDefenderDamage({
        minDamage,
        averageDamage,
        maxDamage,
        minKills,
        averageKills,
        maxKills
      });
    },
    calculateStatsModification(creature, hero) {
      creature.attack += parseInt(hero.attack);

      if (creature.nativeTerrain === this.calculator.terrain) {
        creature.attack++;
        creature.defense++;
      }

      // ATTACKER hero unit specialty bonus
      if (hero.specialtyUnit) {
        if (hero.specialtyUnit.includes(creature.id)) {
          creature.attack,
            (creature.defense *=
              1 + (hero.stats.level / creature.level) * 0.05);

          creature.attack = Math.ceil(creature.attack);
          creature.defense = Math.ceil(creature.defense);
        }
      }

      if (creature.effects.length > 0) {
        // ATTACKER stats buffs

        // Bloodlust spell
        if (creature.effects.includes("Bloodlust")) {
          // Bloodlust spell hero specialty bonus
          if (hero.specialtySpell === "bloodlust") {
            if (creature.level <= 2) {
              creature.attack += 3;
            } else if (creature.level <= 4) {
              creature.attack += 2;
            } else if (creature.level <= 6) {
              creature.attack += 1;
            }
          }

          if (hero.skills.fire <= 1) {
            creature.attack += 3;
          } else if (hero.skills.fire >= 2) {
            creature.attack += 6;
          }
        }

        // Frenzy spell
        if (creature.effects.includes("Frenzy")) {
          if (hero.skills.fire <= 1) {
            creature.attack += creature.defense;
          } else if (hero.skills.fire === 2) {
            creature.attack += creature.defense * 1.5;
          } else if (this.$store.state.calculator.attacker.hero.fire === 3) {
            creature.attack += creature.defense * 2;
          }
          creature.defense = 0;
        }

        // Slayer spell
        if (creature.effects.includes("Slayer")) {
          let units = [
            26,
            27,
            54,
            55,
            68,
            69,
            96,
            97,
            110,
            111,
            137,
            138,
            139,
            140
          ];
          if (hero.skills.fire === 2) {
            units.push(12, 13, 82, 83);
          } else if (hero.skills.fire === 3) {
            units.push(40, 41, 124, 125, 154, 155);
          }

          // If DEFENDER unit in slayer units list
          if (units.includes(this.defender.creature.id)) {
            creature.attack += 8;
          }
        }

        // Prayer spell
        if (creature.effects.includes("Prayer")) {
          // Prayer spell hero specialty bonus
          if (hero.specialtySpell === "Prayer") {
            if (creature.level <= 2) {
              creature.attack, (creature.defense += 3);
            } else if (creature.level <= 4) {
              creature.attack, (creature.defense += 2);
            } else if (creature.level <= 6) {
              creature.attack, (creature.defense += 1);
            }
          }

          if (hero.skills.water <= 1) {
            creature.attack, (creature.defense += 2);
          } else if (hero.skills.water >= 2) {
            creature.attack, (creature.defense += 4);
          }
        }

        // Precision spell
        if (creature.effects.includes("Precision") && creature.ranged) {
          // Precision hero specialty bonus
          if (hero.skills.specialtySpell === "Precision") {
            if (creature.level <= 2) {
              creature.attack += 3;
            } else if (creature.level <= 4) {
              creature.attack += 2;
            } else if (creature.level <= 6) {
              creature.attack += 1;
            }
          }

          if (hero.skills.air <= 1) {
            creature.attack += 3;
          } else if (hero.skills.air >= 2) {
            creature.attack += 6;
          }
        }

        // Stone skin spell
        if (creature.effects.includes("Stone Skin")) {
          // Stone skin hero specialty bonus
          if (hero.specialtySpell === "Stone Skin") {
            if (creature.level <= 2) {
              creature.defense += 3;
            } else if (creature.level <= 4) {
              creature.defense += 2;
            } else if (creature.level <= 6) {
              creature.defense += 1;
            }
          }

          if (hero.skills.earth <= 1) {
            creature.defense += 3;
          } else if (hero.skills.earth >= 2) {
            creature.defense += 6;
          }
        }

        // ATTACKER stats debuffs

        // Weakness spell
        if (creature.effects.includes("Weakness")) {
          // Weakness hero specialty bonus
          if (hero.specialtySpell === "weakness") {
            if (creature.level <= 2) {
              creature.attack -= 3;
            } else if (creature.level <= 4) {
              creature.attack -= 2;
            } else if (creature.level <= 6) {
              creature.attack -= 1;
            }
          }

          if (this.defender.hero.skills.water <= 1) {
            creature.attack -= 3;
          } else if (this.defender.hero.skills.water >= 2) {
            creature.attack -= 6;
          }
        }

        // Disrupting Ray spell
        if (creature.effects.includes("Disrupting Ray")) {
          // Disrupting Ray hero specialty bonus
          if (this.defender.hero.specialtySpell === "Disrupting Ray") {
            creature.defense -= 2;
          }

          if (this.defender.hero.skills.air <= 1) {
            creature.defense -= 3;
          } else if (this.defender.hero.skills.air === 2) {
            creature.defense -= 4;
          } else if (this.defender.hero.skills.air === 3) {
            creature.defense -= 5;
          }
        }
      }
    },

    calculateBlessModificator(creature, hero) {
      if (creature.effects.includes("Bless")) {
        if (hero.specialtySpell === "bless") {
          creature.damageBonus += (0.03 * hero.stats.level) / creature.level;
        }
        if (hero.skills.water <= 1) {
          creature.baseMinDamage = creature.baseMaxDamage;
        } else if (hero.skills.water >= 2) {
          creature.baseMinDamage = creature.baseMaxDamage + 1;
          creature.baseMaxDamage++;
        }
      }
      return creature;
    },
    calculateCurseModificator(creature, hero) {
      if (creature.effects.includes("Curse")) {
        if (hero.skills.fire <= 1) {
          creature.baseMaxDamage = creature.baseMinDamage;
        } else if (hero.skills.fire >= 2) {
          creature.baseMinDamage = creature.baseMinDamage * 0.8 - 1;
          creature.baseMaxDamage = creature.baseMinDamage;
        }
      }
      return creature;
    },
    calculateTotalDamagesModification(creature, hero) {
      let levelBonus,
        damageBonus,
        defenseBonus,
        defenseMagicBonus = 0;

      if (hero.id) {
        levelBonus = 1 + 0.05 * hero.stats.level;
      }

      // Offense skill bonus
      if (hero.skills.offense > 0 && !creature.ranged) {
        let bonus = 0.1 * hero.skills.offense;
        if (hero.specialtySkill === "offense") {
          bonus *= levelBonus;
          if (bonus > 1.92) {
            // 1.92 max bonus
            bonus = 1.92;
          }
        }
        damageBonus += bonus;
      }

      // Armorer skill bonus
      if (hero.skills.armorer > 0) {
        let bonus = 0.05 * hero.skills.armorer;
        if (hero.specialtySkill === "armorer") {
          bonus *= levelBonus;
          if (bonus > 0.7) {
            // 0.7 max bonus
            bonus = 0.7;
          }
        }
        defenseBonus += bonus;
      }

      // Archery skill bonus
      if (hero.skills.archery > 0 && creature.ranged) {
        let bonus = 0;
        if (hero.skills.archery === 1) {
          bonus = 0.1;
        } else if (hero.skills.archery === 2) {
          bonus = 0.25;
        } else if (hero.skills.archery === 3) {
          bonus = 0.5;
        }

        if (hero.specialtySkill === "archery") {
          bonus *= levelBonus;
          if (bonus > 3.2) {
            // 3.2 max bonus
            bonus = 3.2;
          }
        }
        damageBonus += bonus;
      }

      // Artillery skill bonus (for ballista and cannon only)
      if (
        hero.skills.artillery > 0 &&
        (creature.id === 158 || creature.id === 159)
      ) {
        if (hero.skills.artillery === 3) {
          creature.baseMinDamage *= 2;
          creature.baseMaxDamage *= 2;
        }
      }

      // Shield spell
      if (creature.effects.includes("Shield")) {
        if (hero.skills.earth <= 1) {
          defenseMagicBonus += 0.15;
        } else if (hero.skills.earth >= 2) {
          defenseMagicBonus += 0.3;
        }
      }

      return {
        ...creature,
        damageBonus,
        defenseBonus,
        defenseMagicBonus
      };

      // DEFENDER air shield spell
      // if (
      //   creature.effects.includes(
      //     "Air Shield"
      //   ) &&
      //   this.$store.state.calculator.defender.unit.ranged
      // ) {
      //   if (this.$store.state.calculator.attacker.hero.air < 2) {
      //     this.attacker.defenseMagicBonus += 0.25;
      //   } else if (this.$store.state.calculator.attacker.hero.air > 1) {
      //     this.attacker.defenseMagicBonus += 0.5;
      //   }
      // }

      // DEFENDER air shield spell
      // if (
      //   this.$store.state.calculator.defender.unit.effects.includes(
      //     "Air Shield"
      //   ) &&
      //   creature.ranged
      // ) {
      //   if (this.$store.state.calculator.defender.hero.air < 2) {
      //     this.defender.defenseMagicBonus += 0.25;
      //   } else if (this.$store.state.calculator.defender.hero.air > 1) {
      //     this.defender.defenseMagicBonus += 0.5;
      //   }
      // }
    },
    calculateWithHates(creature) {
      const hate = this.data.hates.filter(creature => {
        return (
          creature.id === this.attacker.activeCreature.id ||
          creature.id === this.defender.activeCreature.id
        );
      });
      if (hate.length > 0) {
        return (creature.damageBonus += 0.5);
      }
    },
    calculateArtillery(creature) {
      // switch (creature.id) {
      //   case 158:
      //     creature.baseMinDamage *= creature.attack - creature; // Behemoth
      //     break;
      //   case 159:
      //     creature.defense *= 0.2; // Ancient Behemoth
      //     break;
      //   case 152:
      //     creature.attack *= 0.7; // Nix
      //     break;
      //   case 153:
      //     creature.attack *= 0.4; // Nix-Warrior
      //     break;
      // }
      // return creature;

      if (creature.id === 158) {
        this.attacker.baseMinDamage +=
          (creature.attack - creature.stats.attack) * 2;
        this.attacker.baseMaxDamage +=
          (creature.attack - creature.stats.attack) * 3;
      } else if (creature.id === 159) {
        this.attacker.baseMinDamage +=
          (creature.attack - creature.stats.attack) * 4;
        this.attacker.baseMaxDamage +=
          (creature.attack - creature.stats.attack) * 7;
      }

      if (this.$store.state.calculator.defender.unit.id === 158) {
        this.defender.baseMinDamage +=
          (this.defender.attack -
            this.$store.state.calculator.defender.unit.stats.attack) *
          2;
        this.defender.baseMaxDamage +=
          (this.defender.attack -
            this.$store.state.calculator.defender.unit.stats.attack) *
          3;
      } else if (this.$store.state.calculator.defender.unit.id === 159) {
        this.defender.baseMinDamage +=
          (this.defender.attack -
            this.$store.state.calculator.defender.unit.stats.attack) *
          4;
        this.defender.baseMaxDamage +=
          (this.defender.attack -
            this.$store.state.calculator.defender.unit.stats.attack) *
          7;
      }
    },
    unitsFeatures(creature) {
      switch (creature.id) {
        case 110:
          creature.defense *= 0.6; // Behemoth
          break;
        case 111:
          creature.defense *= 0.2; // Ancient Behemoth
          break;
        case 152:
          creature.attack *= 0.7; // Nix
          break;
        case 153:
          creature.attack *= 0.4; // Nix-Warrior
          break;
      }
      return creature;
    }
  }
};
</script>

