<template>
  <div class="select-skill">
    <span>{{ skill }}</span>
    <div class="levels">
      <button
        v-for="(level, index) in levels"
        :key="level"
        :class="{ active: selectedLevel === index + 1 }"
        @click="onClick(index + 1)"
      >
        {{ level }}
      </button>
    </div>

    <div class="levels-select">
      <select>
        <option value="value1">Значение 1</option>
      </select>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    skill: {
      value: String,
      default: "",
      required: true
    }
  },
  data() {
    return {
      selectedLevel: 0,
      levels: ["Basic", "Advanced", "Expert"]
    };
  },
  methods: {
    onClick(level) {
      if (level === this.selectedLevel) {
        this.selectedLevel = 0;
      } else {
        this.selectedLevel = level;
      }
      this.$emit("click", this.selectedLevel);
    }
  }
};
</script>

<style lang="scss">
.select-skill {
  display: flex;
  flex-direction: row;
  align-items: center;

  span {
    flex: 0 0 20%;
  }

  .levels {
    padding-left: 5px;
    padding-right: 5px;

    button {
      @extend %button;
      font-size: 0.9em;
      height: 25px;
      width: 75px;
      color: rgb(40, 167, 69);
      border-top: 1px solid rgb(40, 167, 69);
      border-bottom: 1px solid rgb(40, 167, 69);
      background-color: transparent;
      transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
        border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;

      &:hover,
      &.active {
        color: rgb(255, 255, 255);
        background-color: rgb(40, 167, 69);
        border-color: rgb(40, 167, 69);
      }

      &:nth-child(1) {
        border-left: 1px solid rgb(40, 167, 69);
      }

      &:nth-last-child(1) {
        border-right: 1px solid rgb(40, 167, 69);
      }
    }
  }
}

@media screen and (min-width: map-get($grid-breakpoints, "large")) {
  .levels-select {
    display: none;
  }
}

@media screen and (max-width: map-get($grid-breakpoints, "large")) {
  .select-skill {
    span {
      flex: 0 0 40%;
    }

    .levels {
      display: none;
    }

    .levels-select {
      flex: 0 0 60%;

      select {
        display: block;
      }
    }
  }
}
</style>
